* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

root {
  height: 14px;
  width: 14px;
  font-size: 14px;
}

.container_all {
  display: flex;
  background-image: url("/tla/photo-1534796636912-3b95b3ab5986.jpeg");
  /* fallback for old browsers */
}

.left_side {
  height: 100vh;
  width: 70vw;
  display: flex;
  flex-direction: column;
}

.left_side .left_side_upper {
  width: 100%;
  height: 70%;
}

.left_side .left_side_bottom_all {
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: column;
}

.left_side .left_side_bottom_all .left_side_bottom_question {
  height: 100%;
  width: 97%;
  border: 2px solid gold;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.left_side .left_side_bottom {
  width: 100%;
  height: 70%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.left_side .left_side_bottom .A:hover, .left_side .left_side_bottom .B:hover, .left_side .left_side_bottom .C:hover, .left_side .left_side_bottom .D:hover {
  transition: 0.5s;
}

.left_side .left_side_bottom .A, .left_side .left_side_bottom .B, .left_side .left_side_bottom .C, .left_side .left_side_bottom .D {
  width: 47%;
  height: 34%;
  border-radius: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 5px 10px 5px;
  border: 2px solid gold;
  background-color: black;
}

.left_side .left_side_bottom .answers {
  font-weight: 600;
  font-size: 1.5rem;
  color: gold;
}

.left_side .left_side_bottom_question_container {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.answer_append {
  display: none;
}

@keyframes pulseAnim {
  0% {
    background: darkgreen;
  }
  100% {
    background: white;
  }
}

@keyframes pulseAnimfail {
  0% {
    background: red;
  }
  100% {
    background: white;
  }
}

.right_side {
  width: 30vw;
  height: 79vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.right_append {
  width: 100%;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 5px 10px 5px;
  border: 2px solid blue;
  background-color: black;
  color: gold;
}

.right_append_question {
  font-size: 1.5rem;
  color: gold;
  display: none;
}

.right_side_container {
  height: 100vh;
  width: 30vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
